
<template>
    <div>
        <b-breadcrumb :items="items"></b-breadcrumb>

        <DetailHeader :des="desData"></DetailHeader>

        <div>
            <p>step 1：pull your mysql with version </p>
            <codemirror 
                value="docker pull mysql:**"
                :options="cmOptions"
                class="code">
            </codemirror>
            <p>step 2：create docker volume </p>
            <p>if no volume,once the docker container is removed , the data in container will gone </p>
            <codemirror 
                value="docker volume create mysql_data"
                :options="cmOptions"
                class="code">
            </codemirror>
            <p>step 3：run image </p>
            <codemirror 
                value="docker run --name mysql -p 3306:3306 -v mysql_data:/var/lib/mysql -e MYSQL_ROOT_PASSWORD=123456 -d mysql:**"
                :options="cmOptions"
                class="code">
            </codemirror>
            <p>
                after that,login in the navicat modify the user account and permissons
            </p>
        </div>

    </div>
</template>

